<template>
  <div id="app">
    <md-card>
      <md-card-header>
        <div class="md-title">Service Support</div>
      </md-card-header>

      <md-card-content>
        Are you having problems? Send your page to the admin so they can recheck your page with weaponlyzer in /check and look for any issues.
      </md-card-content>
      </md-card>
      <form class="md-layout">
      <md-field md-inline>
        <label class="input">http://watcher.pwn2.win/pages/2555deea69c98b9188d8fccda991d5d3cef9e9a5e514a8b23f906d078f2a7545/0b1ff1fbca9c.html</label>
        <span class="md-prefix">http://watcher.pwn2.win/</span>
        <md-input v-model="initial" class="input"></md-input>
        <md-card-actions>
            <md-button class="md-primary" @click="send">Send</md-button>
        </md-card-actions>
      </md-field>
      <vue-recaptcha sitekey="6Ldr6vYUAAAAAGonpRXGwucElCqfCCCGTWIkie4x" @verify="onVerify" :loadRecaptchaScript="true"></vue-recaptcha>

      <md-snackbar :md-active.sync="sent">{{ message }}</md-snackbar>
    </form>

  </div>
</template>

<script>
  import axios from 'axios';
  import VueRecaptcha from 'vue-recaptcha';
  
  export default {
    components: {
      VueRecaptcha,
    },
    data: () => ({
      initial: '',
      empty: '',
      sent: false,
      token: '',
      message: 'Admin will check your url'
    }),

    methods: {
      send() {
        axios.post('/contact.php', {
          page: this.initial,
          'g-recaptcha-response': this.token
        })
        .then(response => {
          if (response.status == 200) {
            if (response.data === 'invalid captcha')
              this.message = 'Invalid captcha!'
            this.sent = true
          }           
          
        })
      },
      onVerify: function (response) {
        this.token = response
      },
    }
  }
</script>

