<template>
    <div id="app">
        <form method="GET" action="/check">
            <md-field md-inline>
                <span class="md-prefix">http://watcher.pwn2.win/</span>
                <md-input v-model="initial" name="url" class="input"></md-input>
                <md-card-actions>
                    <md-button class="md-primary" @click="checkUrl">Check</md-button>
                </md-card-actions>
            </md-field>
             <vue-recaptcha sitekey="6Ldr6vYUAAAAAGonpRXGwucElCqfCCCGTWIkie4x" @verify="onVerify" :loadRecaptchaScript="true"></vue-recaptcha>
        </form>
        <div id="container">
            <h3 class="md-title">{{error.data}}</h3>
            <md-list v-for="(value, name) in weapon.data" v-bind:key="value">
                <md-list-item class="wappalyzer-color" name="url">{{name}}</md-list-item>
                <md-list-item v-for="data in value" v-bind:key="data">{{data.name}}<code v-html="data.version"></code></md-list-item>
            </md-list>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    import VueRecaptcha from 'vue-recaptcha';

    export default {
        components: {
        VueRecaptcha,
        },
        data: () => ({
            initial: 'pages/2555deea69c98b9188d8fccda991d5d3cef9e9a5e514a8b23f906d078f2a7545/0b1ff1fbca9c.html',
            empty: '',
            weapon: {
                display: false,
                data: ''
            },
            error: {
                display: false,
                data: ''
            },
            token: ''
        }),
        mounted() {
            if (this.$route.query.url && this.$route.query.token) {
                    axios.post('/check.php', {
                        url: this.$route.query.url,
                        token: this.$route.query.token
                    })
                    .then(response => {
                        this.error.display = true
                        if (response.data === 'content is too long')
                            this.error.data = 'Error: Unfortunately our Static Web Host doesn\'t support pages larger than 1500 characters :('
                        else if (response.data === 'redirects are not allowed')
                            this.error.data = 'Error: Our server currently cannot understand meta refresh :('
                        else if (/Couldn't analyze your file/.test(response.data))
                            this.error.data = response.data
                        else {///} (response.data == Object) {
                            this.error.display = false
                            this.weapon.display = true
                            this.weapon.data = response.data
                    // } else {
                        // this.error.data = 'Strange error, you must be a strange person'
                        }
                    })

            }
        },
        methods: {
            checkUrl() {
                axios.post('/check.php', {
                    url: this.initial,
                    'g-recaptcha-response': this.token
                })
                .then(response => {
                    this.error.display = true
                    if (response.data === 'content is too long')
                        this.error.data = 'Error: Unfortunately our Static Web Host doesn\'t support pages larger than 1500 characters :('
                    else if (response.data === 'redirects are not allowed')
                        this.error.data = 'Error: Our server currently cannot understand meta refresh :('
                    else if (response.data === 'invalid captcha')
                        this.error.data = 'Error: Invalid Recaptcha :/'
                    else if (/Couldn't analyze your file/.test(response.data))
                        this.error.data = response.data
                    else {///} (response.data == Object) {
                        this.error.display = false
                        this.weapon.display = true
                        this.weapon.data = response.data
                // } else {
                    // this.error.data = 'Strange error, you must be a strange person'
                    }
                })
        },
            onVerify: function (response) {
                this.token = response
            },
        }
    }
</script>

<style scoped>
  #app {
    padding: 50px 400px 0px 400px !important;
  }
 .input {
    margin-top: 10px;
  }
 .md-prefix {
    padding: 0px !important;
  }
  .weaponlyzer {
    background-color: #4608ad !important;
    color: white !important;
    width: 100%;
    margin-left: 0px !important;
  }
  .error {
    background: #e2777a !important;
    margin-bottom: 10px !important;
  }
  .wappalyzer-color {
    background: #4608ad !important;
    color: white !important;
  }
  .wappalyzer-color > * {
    color: white !important;
  }
  .md-title {
    white-space: break-spaces !important;
  }

</style>