<template>
  <div id="app">
    <md-toolbar v-if="error.display" class="md-accent error">
      <h3 class="md-title">{{error.data}}</h3>
    </md-toolbar>
    <PageEditor @update:home="(code) => updateHome(code)" @update:about="(code) => updateAbout(code)" @update:contact="(code) => updateContact(code)" />
    <div id="container">
        <md-button class="md-buttons weaponlyzer" v-on:click="upload">Weaponlyzer</md-button>
        <vue-recaptcha class="recaptcha" sitekey="6Ldr6vYUAAAAAGonpRXGwucElCqfCCCGTWIkie4x" @verify="onVerify" :loadRecaptchaScript="true"></vue-recaptcha>

        <md-list v-for="(value, name) in weapon.data" v-bind:key="value">
          <md-list-item class="wappalyzer-color">{{name}}</md-list-item>
          <md-list-item v-for="data in value" v-bind:key="data">{{data.name}}<code v-html="data.version"></code></md-list-item>
        </md-list>
    </div>
    <md-snackbar :md-active.sync="sent">Analyzing your application technologies</md-snackbar>
  </div>
</template>

<script>
  import PageEditor from '@/components/PageEditor.vue';
  import VueRecaptcha from 'vue-recaptcha';
  import axios from 'axios';

  export default {
    components: {
      PageEditor,
      VueRecaptcha,
    },
    data () {
      return {
        error: {
          display: false,
          data: ''
        },
        weapon: {
          display: false,
          data: ''
        },
        editor: {
          home: " <h1>Hackers must die!!1</h1>\n\nand other things you gonna learn in my three pages static site",
          about:  "How weaponlyzer helped me to increase my security monitoring the technologies I use",
          contact: "please email me at secure@concerned.dev"
        },
        sent: false,
        token: ''
      }
    },
    methods: {
      upload: function () {
        if (this.token != '')
          this.sent = true
        this.error.display = false
        this.weapon.display = false
          axios.post('/upload.php', {
            home: this.editor.home,
            about: this.editor.about,
            contact: this.editor.contact,
            'g-recaptcha-response': this.token
          })
          .then(response => {
              this.error.display = true
              this.token = ''
              if (response.data === 'content is too long')
                this.error.data = 'Error: Unfortunately our Static Web Host doesn\'t support pages larger than 1500 characters :('
              else if (response.data === 'redirects are not allowed')
                this.error.data = 'Error: Our server currently cannot understand meta refresh :('
              else if (/Couldn't analyze your file/.test(response.data))
                this.error.data = response.data
              else if (response.data === 'invalid captcha')
                this.error.data = 'Error: Invalid Recaptcha :/'
              else if (response.data["Web servers"]) {
                this.sent = true
                this.error.display = false
                this.weapon.display = true
                this.weapon.data = response.data
              } else {
                this.error.data = 'Strange error, you must be a strange person'
              }
          })
      },
      updateHome(code) {
        this.editor.home = code
      },
      updateAbout(code) {
        this.editor.about = code
      },
      updateContact(code) {
        this.editor.contact = code
      },
      onVerify: function (response) {
        this.token = response
      },
    }
  }
</script>

