  .recaptcha {
    margin-top: 18px;
  }
  body {
      margin: 0;
  }
  #app {
      padding: 50px 400px 0px 400px;
  }
  #container {
    width: 100%;
    padding: 0px 16px;
  }
  .weaponlyzer {
    background-color: #4608ad !important;
    color: white !important;
    width: 100%;
    margin-left: 0px !important;
  }
  .error {
    background: #e2777a !important;
    margin-bottom: 10px !important;
  }
  .wappalyzer-color {
    background: #4608ad !important;
    color: white !important;
  }
  .wappalyzer-color > * {
    color: white !important;
  }
  .md-title {
    white-space: break-spaces !important;
  }

</style>